.error-alert {
  margin: 0 1rem 1rem;
  text-align: left;
  font-size: 1rem;
  border: .1rem solid coral;
  background-color: lightpink;
  border-radius: .2rem;
  color: brown;
}

.error-alert .error {
  padding-left: 1rem;
}
.login {
  margin: auto;
  width: 30%;
  min-width: 40rem;
  padding-top: 20rem;
}

.login .title {
  text-align: center;
  font-size: 3rem;
  padding-bottom: 2rem;
}

.login-label {
  display: block;
  font-size: 1.5rem;
  padding: .3rem;
}

.token-input, .login-button {
  width: 100%;
  line-height: 2rem;
}

.profile {
  display: flex;
  height: 6rem;
  background-color: white;
}

.profile:hover {
  background-color: var(--light-orange);
  border: none;
}

.profile .name {
  width: 5rem;
  font-size: 1.2rem;
  font-weight: 600;
}

.profile--selected {
  background-color: var(--light-orange);
  border: none;
}

.profile-picture {
  flex-grow: 1;
}

.profile-picture img {
  height: 6rem;
  max-width: 4.5rem;
  min-width: 4.5rem;
}

.profile-info {
  flex-grow: 7;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
}

.profile-info p {
  margin: 0 1rem;
  font-size: 1.2rem;
}
#search .title {
  font-size: 2rem;
  text-transform: uppercase;
  padding-bottom: 1rem;
}

#search {
  padding: 2rem;
}

.search-input {
  min-width: 20rem;
  line-height: 2rem;
}

.profile-list {
  max-height: 50rem;
  overflow-y: scroll;
  padding-top: 1rem;
}
.home {
  display: grid;
  grid-template-columns: repeat(4, [cols] 1fr);
  grid-template-rows: 60rem 1fr;
}

.search-hero {
  grid-column: 1 / 2 span;
  grid-row-start: 1;
}

.search-villain {
  grid-column: 3 / 2 span;
  grid-row-start: 1;
  padding-left: 0 !important;
}

.home button {
  grid-column-start: 3;
  align-self: center;
  margin: 0 1rem 1rem;
}

.home .error-alert {
  grid-column-start: 2;
  align-self: center;
}

.compare-button {
  height: 3.3rem;
}
.character img {
  height: 25rem;
}

.character {
  display: flex;
  padding: 1rem;
}

.character-details p {
  margin: 0 1rem;
  text-transform: capitalize;
  font-size: 2rem;
  flex-grow: 2;
  background-color: white;
  padding: .1rem 0;
}
.result {
  display: grid;
  grid-template-columns: repeat(4, [cols] 1fr);
  grid-template-rows: 1fr 60rem 1fr;
}

.character-hero {
  grid-column: 3 / 2 span;
  grid-row-start: 2;
  padding-left: 0 !important;
}

.character-villain {
  grid-column: 1 / 2 span;
  grid-row-start: 2;
}

.result button {
  grid-column-start: 3;
  grid-row-start: 3;
  align-self: center;
}

.result .title {
  grid-row-start: 1;
  font-size: 2rem;
  padding: 1rem;
  font-weight: 600;
}
.overlay:before {
  content: '';
  background-color: white;
  width: 100%;
  height: 100%;
  opacity: .6;
  position: absolute;
  z-index: 99;
}

@-webkit-keyframes spinner {
  0% {
    -webkit-transform: translate3d(-50%, -50%, 0) rotate(0deg);
            transform: translate3d(-50%, -50%, 0) rotate(0deg);
  }
  100% {
    -webkit-transform: translate3d(-50%, -50%, 0) rotate(360deg);
            transform: translate3d(-50%, -50%, 0) rotate(360deg);
  }
}

@keyframes spinner {
  0% {
    -webkit-transform: translate3d(-50%, -50%, 0) rotate(0deg);
            transform: translate3d(-50%, -50%, 0) rotate(0deg);
  }
  100% {
    -webkit-transform: translate3d(-50%, -50%, 0) rotate(360deg);
            transform: translate3d(-50%, -50%, 0) rotate(360deg);
  }
}

.spinner {
  top: 30rem;
  opacity: 1;
  position: relative;
  transition: opacity linear 0.1s;
  z-index: 999;
}

.spinner::before {
  -webkit-animation: 2s linear infinite spinner;
          animation: 2s linear infinite spinner;
  border: solid 3px #eee;
  border-bottom-color: var(--blue);
  border-radius: 50%;
  content: "";
  height: 4rem;
  left: 50%;
  opacity: inherit;
  position: absolute;
  top: 50%;
  -webkit-transform: translate3d(-50%, -50%, 0);
          transform: translate3d(-50%, -50%, 0);
  -webkit-transform-origin: center;
          transform-origin: center;
  width: 4rem;
  will-change: transform;
}

.app {
  height: 100%;
  background-color: #f9f9f9;
}
html {
  font-size: 62.5%;
  font-family: Lato,'Helvetica Neue',Arial,Helvetica,sans-serif;
}

body {
  margin: 0;
  padding: 0;
}

html, body, #root {
  height: 100%;
}

:root {
  --orange: #ee6123;
  --light-orange: #fdefe9;
  --dark-grey: #444444;
  --light-grey: #9b9b9b;
  --lightest-grey: #ececec;
  --blue: #55b1dc;
}

.container {
  padding: 1rem;
}

a {
  text-decoration: none;
}

button:hover, button:focus {
  cursor: pointer;
  background-color: #9b9b9b;
  background-color: var(--light-grey);
}

fieldset {
  border: none;
  margin: 0;
  padding: 1rem;
}

input {
  box-sizing: border-box;
}

button {
  border-radius: 0;
  padding: .5rem;
  background-color: #444444;
  background-color: var(--dark-grey);
  color: white;
  border: none;
}

